import React from "react";
import { makeStyles } from "@mui/styles";
import { Container, Typography, Grid, Box } from "@mui/material";
import SchoolLogo from "../assets/logo.png";
import PowerSettingsNewIcon from "@mui/icons-material/PowerSettingsNew";
import Fab from "@mui/material/Fab";
import { Outlet, useNavigate } from "react-router-dom";
import authenticationService from "../services/authentication";
import useCurrentUser from "../hooks/useCurrentUser";

const useStyles = makeStyles((theme) => ({
  root: {},
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardContent: {
    flexGrow: 1,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  gridContainer: {
    maxWidth: 800,
    margin: "0 auto",
  },
  examTitleText: {
    wordBreak: "break-word",
  },
}));

const StudentsLayout = () => {
  const classes = useStyles();
  const navigate = useNavigate();

  const { user } = useCurrentUser();

  console.log("user::: ", user);

  const handleLogout = (event) => {
    event.stopPropagation();

    authenticationService.destroyUserSession();

    navigate("/");
  };

  return (
    <Box
      bgcolor="rgb(237, 238, 241)"
      minHeight="100vh"
      display="flex"
      flexDirection="column"
      justifyContent="space-between"
    >
      <Box>
        <Box
          position="relative"
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          height={48}
          maxHeight={48}
          bgcolor="#FFFFFF"
          borderBottom={1}
          borderColor="rgb(229, 231, 235)"
          flexGrow={1}
          pl={3}
          pr={3}
          py={1}
          gap={2}
        >
          <Box
            display="flex"
            alignItems="center"
            flex={1}
            justifyContent="space-between"
          >
            <Box
              className={classes.TeamLogoContainer}
              display="flex"
              flexDirection="row"
              alignItems="center"
              justifyContent="flex-start"
            >
              <img src={SchoolLogo} alt="Logo" className={classes.avatar} />
            </Box>

            <Box>
              <Fab variant="extended" size="small" onClick={handleLogout}>
                <PowerSettingsNewIcon />
                <Typography variant="subtitle" gutterBottom>
                  Logout
                </Typography>
              </Fab>
            </Box>
          </Box>
        </Box>
        <Container component="main" maxWidth={false} className={classes.root}>
          <Outlet />
        </Container>
      </Box>
      <Box py={3}>
        <Typography fontSize={12} fontWeight="bold" marginBottom={1}>
          Logged in as: {user.user_firstname} {user.user_lastname} (
          {user.user_name})
        </Typography>
        <Typography fontSize={12}>
          PhronXams ver. 1.0.1 - Copyright © 2021-2024 Signet AutoSys - Signet
          Autosis LTD
        </Typography>
      </Box>
    </Box>
  );
};

export default StudentsLayout;
